.header {
	&__top {
		margin-bottom: 10px;
		padding-top: 10px;
		padding-bottom: 10px;
		background-color: #B83020;
	}

	&__top-nav {
		display: flex;
		justify-content: flex-end;
	}

	&__top-el {
		position: relative;
		padding: 0 13px;

		&:first-child {
			padding-left: 0;
		}

		&:last-child {
			padding-right: 0;
		}

		&:not(:last-child) {
			&::after {
				position: absolute;
				top: 50%;
				right: 0;
				transform: translateY(-50%);
				height: 14px;
				border-right: 1px solid #fff;
				content: "";
			}
		}
	}

	&__top-link {
		font-family: $font-secondary;
		font-size: 12px;
		line-height: 14px;
		color: #fff;

		&:hover {
			text-decoration: underline;
		}
	}

	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__logo-box {
		display: inline-block;
	}

	&__logo {
		display: block;
	}

	&__nav-box {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 40px;
	}

	&__menu-btn {
		display: inline-flex;
		align-items: center;
		gap: 12px;
		padding: 13px 30px;
		background-color: #3D4B88;
		border-radius: 100px;
		border: none;
		font-family: $font-secondary;
		font-size: 14px;
		line-height: 16px;
		font-weight: 600;
		color: #fff;
		white-space: nowrap;
	}

	&__menu-icon {
		flex-shrink: 0;
		width: 24px;
		height: 24px;
	}

	&__nav {
	}

	&__nav-list {
		display: flex;
		align-items: center;
		gap: 40px;
	}

	&__nav-el {
	}

	&__nav-link {
		font-family: $font-secondary;
		font-size: 14px;
		line-height: 16px;

		&:hover {
			text-decoration: underline;
		}
	}

	&__action-list {
		display: flex;
		align-items: center;
		gap: 10px;
	}

	&__action-el {
		flex-shrink: 0;
	}

	&__action-link {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		height: 50px;
		border-radius: 50px;
	}

	&__action-icon {
		display: block;
		width: 30px;
		height: 30px;
	}
}
