.button {
	position: relative;
	display: inline-block;
	padding: 10px 30px;
	border: none;
	border-radius: 60px;
	font-size: 14px;
	line-height: 17px;
	font-weight: 600;
	white-space: nowrap;
	text-align: center;
	transition: color $transition-default, background-color $transition-default;

	&:hover {
	}

	&--violet {
		background-color: #E7EDF7;
		color: #003584;
	}

	&--green {
	}

	&--blue {
		background-color: #3D4B88;
		color: #fff;
	}

	&--gray {
		background-color: #F2F4F5;
		color: #898989;
	}
}
