.section-directions {
	padding-top: 50px;
	padding-bottom: 50px;

	&__title {
		margin-bottom: 24px;
	}

	&__list {
		display: grid;
		grid-template: auto / repeat(3, minmax(0,1fr));
		grid-gap: 20px;
	}

	&__el {
	}

	&__link {
		@include main-shadow;
		display: block;
		padding: 20px;
		font-size: 16px;
		line-height: 20px;
		border-radius: 6px;
	}
}
