.filter {
	&__search {
		margin-bottom: 20px;
	}

	&__group {
		margin-bottom: 20px;
	}

	&__group-top {
		display: flex;
		align-items: center;
		gap: 10px;
		margin-bottom: 10px;
		padding: 0;
		border: none;
		background-color: transparent;
	}

	&__group-top-text {
		font-size: 16px;
		line-height: 20px;
		font-weight: 700;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	&__group-arrow {
		flex-shrink: 0;
		display: block;
		width: 20px;
		height: 20px;
		transition: transform $transition-default;

		.filter__group--expanded & {
			transform: rotate(-180deg);
		}
	}

	&__group-list {
		display: none;
		flex-direction: column;
		gap: 8px;

		.filter__group--expanded & {
			display: flex;
		}
	}

	&__group-el {
	}
}
